.MainConsulting {
  &-title {
    margin: 70px 0 68px;
    color: #fff;
    font-size: 34px;
    font-weight: 500;
    text-align: center;
    @media screen and (max-width: 860px) {
      margin: 20px 0 54px;
    }
    @media screen and (max-width: 480px) {
      margin-bottom: 30px;
      font-size: 31px;
    }
  }
  &-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    & .ConsultingCategoryItem {
      &-container {
        width: calc((100% - 60px) / 3);
        @media screen and (max-width: 860px) {
          width: 100%;
        }
      }
    }
  }
}
